<template>
    <div class="login-bg">
        <div class="login-logo">
            <div style="text-align:center;">
                <img src="../assets/logo.jpg">
                <h1 style="margin-top:20px">1</h1>
            </div>
            <div>
                <!-- <h5></h5> -->
            </div>
        </div>
        <div class="login-form">
            <h3 class="login-title">账密登录</h3>
            <div class="login-area">
                <div class="login-item">
                    <img src="../assets/count.png" alt="">
                    <n-input v-model:value="ruleForm.username" type="text" autocomplete="off" placeholder="请输入账号"></n-input>
                </div>
                <div class="login-item">
                    <img src="../assets/pwd.png" alt="">
                    <n-input v-model:value="ruleForm.password" type="password" autocomplete="off" placeholder="请输入密码"></n-input>
                   
                </div>
                <n-button class="login-btn" type="primary" @click="login">登录</n-button>
            </div>
        </div>       
    </div>
    <!-- <operate-in-process :show="showLoading"></operate-in-process> -->
</template>
<script setup>
    import {reactive} from'vue'
    import _axios from '../plugins/axios'
    import { useMessage } from "naive-ui";
    import {setToken} from '@/utils/tools.js'
    import { useRouter,useRoute } from 'vue-router';
    import store from '@/store'
    const router = useRouter()
    const route = useRoute()
    const message = useMessage()
    let messageReactive;
    const ruleForm = reactive({
        username:'',
        password:''
    })
    const login = async ()=>{

        if(!ruleForm.username || !ruleForm.password) return message.error('请输入账号或密码')
        if (!messageReactive) {
            
          messageReactive = message.info("登陆中", {
            duration: 0
          });
        }
        try {
            const res = await _axios('post', '/sys/yunke/mgr/adminlogin', ruleForm);
            setTimeout(() => { 
                if (messageReactive) {
                    messageReactive.destroy();
                    messageReactive = null;
                    router.push('/index')
                    message.success(
                        "登陆成功"
                    )
                }
            }, 1000);
            const {token,userInfo} = res
            store.commit('user/setUserInfo',JSON.stringify(userInfo))
            setToken(token.tokenValue)
            store.commit('user/setToken',JSON.stringify(token))
            
        } catch (error) {
            if (messageReactive) {
                messageReactive.destroy();
                messageReactive = null;
            }
            console.error(error)
            message.error('登陆失败!')
        }
	}
</script>
